
@use "@/assets/scss/_base";

:deep(.main) {
    display: flex;
    min-width: calc(v-bind("moduleConfig.mainLayer.noAircraftsMessage.width") * 1px);
    min-height: 300px;
    align-items: center;
    justify-content: center;
}

.fps {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background: v-bind("moduleConfig.stageBackgroundColor");

    @include base.column-padding;
}
